
import Scroll from "../commion/scroll/Scroll";
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
import { Toast } from  "mint-ui";
export default {
components:{
Loading,
Scroll,     //=> 注册BetterScroll组件
},
data(){
return{
isLoading:false,
newslist:[],
count: 10,
loading: false
}
},
computed:{
...mapState({ ajaxIsLoading: state => state.axios.ajaxIsLoading }),
noMore() { return this.count >= 20 },
disabled() { return this.loading || this.noMore },
},
created(){
this.getNewsList();
},
methods:{

loadMore(){
console.log('------');
},

contentScroll(position){



this.isShowBackTop = (-position) > 1000
},
load () {
this.loading = true;
setTimeout(() => {
this.count += 2;
this.loading = false
}, 1000)
},
onRefresh() {
setTimeout(() => {
this.isLoading = false;
Toast("刷新成功")
}, 2000);
},
getNewsList(){

axios.get('./static/Snake.json',
{
'headers':{
'Content-Type':'Access-Control-Allow-Origin:*,Access-Control-Allow-Method:POST,GET,PUT,DELETE,OPTIONS',
}
}
).then(result => {
if (result.data.status === 0){

this.newslist = result.data.clothing;
}else{
Toast('获取服装资讯失败。。。')
}
},)
},
},
}



